import React, { Fragment } from "react";
// import { Link } from 'react-router-dom';
// import { Col } from '../Grid';
import image from '../../assets/computerChefLogo.png';
import './Nav.css';

const Nav = (props) => {
  let greeting;

  if (props.user === null) {
		greeting = <p>Hello guest</p>
	} else if (props.user.firstName) {
		greeting = (
			<Fragment>
				Welcome back, <strong>{props.user.firstName}</strong>
			</Fragment>
		)
	} else if (props.user.username) {
		greeting = (
			<Fragment>
				Welcome back, <strong>{props.user.username} </strong>
			</Fragment>
		)
  }
  
  return (

    <nav className="navbar navbar-expand-lg">
      <a className="navbar-brand" href="/"><img src={image} height="80px" width="80px" alt=""/></a>
      <div className="app-name">YummyBot</div>
      <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      

      <div className="collapse navbar-collapse" id="navbarSupportedContent">
      
        <ul className="navbar-nav mr-auto">
          <li className="nav-item active">
            <a className="nav-link" href="/">Home <span className="sr-only">(current)</span></a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="/">Add Personal Recipe</a>
          </li>
          <li className="nav-item dropdown">
            <a className="nav-link dropdown-toggle" href="/favoriterecipe" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              Profile
            </a>
            <div className="dropdown-menu" aria-labelledby="navbarDropdown">
              <a className="dropdown-item" href="/favoriterecipe">Favorite Recipes</a>
              <a className="dropdown-item" href="/personalrecipe">Personal Recipes</a>
            </div>
          </li>
          
        </ul>
        
        
      </div>
    </nav>    




  /*
    <nav className="navbar navbar-expand-lg navbar-dark bg-primary">
      <Col size="md-2">
        <Link to="/" className="navbar-brand">React Reading List</Link>
      </Col>
      <Col size="md-7"></Col>
      <Col size="md-3">
        <div className="float-right">
        {greeting} - <Link to="#" className="logout" onClick={props.logout}>Logout</Link>
        </div>
      </Col>
    </nav>
  */

  )
};

export default Nav;



